import { PlaceOrder as PlaceOrderComponent } from '@/checkout/components/PlaceOrder/PlaceOrder';
import { Cart, NegotiableQuote } from '@/checkout/data/models';
import {
  getLatestCheckoutUpdate,
  getValue,
  hasPendingUpdates,
  state,
} from '@/checkout/lib';
import { isSetAddressRequestPending } from '@/checkout/store/ui';
import { ErrorCodes, mapErrorToCode } from '@/checkout/lib/errors';
import { Item as ItemModel } from '@/checkout/types';
import { useText } from '@adobe-commerce/elsie/i18n';
import { Container, Slot, SlotProps } from '@adobe-commerce/elsie/lib';
import { events } from '@adobe-commerce/event-bus';
import { HTMLAttributes } from 'preact/compat';
import { useCallback, useEffect, useState } from 'preact/hooks';

export interface ContentSlotContext {
  code: string;
}

export interface HandlePlaceOrderContext {
  code: string;
  cartId?: string | null;
  quoteId?: string | null;
}

export interface PlaceOrderProps extends HTMLAttributes<HTMLDivElement> {
  disabled?: boolean;
  active?: boolean;
  handleValidation?: () => boolean;
  handlePlaceOrder: (ctx: HandlePlaceOrderContext) => Promise<void>;
  slots?: {
    Content?: SlotProps<ContentSlotContext>;
  };
}

export const PlaceOrder: Container<PlaceOrderProps> = ({
  disabled: disabledViaProp = false,
  active = true,
  handleValidation,
  handlePlaceOrder,
  slots,
  ...props
}) => {
  const [isInitialized, setIsInitialized] = useState(false);
  const [hasOutOfStockItems, setHasOutOfStockItems] = useState(false);

  const hasQueuedUpdates = hasPendingUpdates.value;

  const translations = useText({
    CheckoutUnexpectedError: 'Checkout.ServerError.unexpected',
    placeOrderButton: 'Checkout.PlaceOrder.button',
  });

  const handlePlaceOrderError = useCallback(
    (error: any) => {
      const code = mapErrorToCode(error);

      const message =
        code === ErrorCodes.UNKNOWN_ERROR
          ? translations.CheckoutUnexpectedError
          : error.message;

      events.emit('checkout/error', { message, code });
    },
    [translations]
  );

  const handleClick = useCallback(async () => {
    try {
      if (handleValidation && !handleValidation()) return;

      await handlePlaceOrder({
        cartId: state.cartId,
        quoteId: state.quoteId,
        code: getValue('selectedPaymentMethod')?.code ?? '',
      });
    } catch (error: any) {
      handlePlaceOrderError(error);
    }
  }, [handleValidation, handlePlaceOrder, handlePlaceOrderError]);

  const handleCheckoutData = useCallback(
    (data: Cart | NegotiableQuote | null) => {
      const isEmptyCart = !data || ('isEmpty' in data && data.isEmpty);

      if (isEmptyCart) {
        setHasOutOfStockItems(false);
      }
    },
    []
  );

  useEffect(() => {
    if (active === false) return;

    const onCartData = events.on(
      'cart/initialized',
      (data) => {
        const items = (data?.items || []) as ItemModel[];
        setHasOutOfStockItems(
          items.some((item) => item.outOfStock || item.insufficientQuantity)
        );
      },
      { eager: true }
    );

    return () => {
      onCartData?.off();
    };
  }, [active]);

  useEffect(() => {
    if (!active) return;

    const pastUpdate = getLatestCheckoutUpdate();

    if (pastUpdate) {
      setIsInitialized(true);
      handleCheckoutData(pastUpdate);
      return;
    }

    const onCheckoutInit = events.on(
      'checkout/initialized',
      (data) => {
        setIsInitialized(true);
        handleCheckoutData(data);
      },
      { eager: true }
    );

    return () => {
      onCheckoutInit?.off();
    };
  }, [active, handleCheckoutData]);

  useEffect(() => {
    if (!active) return;

    const onCheckoutUpdated = events.on(
      'checkout/updated',
      (data) => {
        handleCheckoutData(data);
      },
      { eager: false }
    );

    return () => {
      onCheckoutUpdated?.off();
    };
  }, [active, handleCheckoutData]);

  return (
    <PlaceOrderComponent
      {...props}
      disabled={
        disabledViaProp ||
        hasOutOfStockItems ||
        hasQueuedUpdates ||
        isSetAddressRequestPending.value
      }
      initialized={isInitialized}
      visible={active}
      onClick={handleClick}
    >
      <Slot
        context={{ code: getValue('selectedPaymentMethod')?.code ?? '' }}
        name="Content"
        slot={slots?.Content}
      >
        {translations.placeOrderButton}
      </Slot>
    </PlaceOrderComponent>
  );
};
