import {
  classifiers,
  ErrorCode,
  ErrorCodes,
} from '@/checkout/lib/errors/classifiers';

export function mapErrorToCode(error: unknown): ErrorCode {
  const matchingClassifier = classifiers.find((classifier) =>
    classifier.matches(error)
  );

  if (matchingClassifier) {
    return matchingClassifier.code;
  }

  return ErrorCodes.UNKNOWN_ERROR;
}
