import { WithConditionals } from '@/checkout/components/ConditionalWrapper/index';
import '@/checkout/components/PlaceOrder/PlaceOrder.css';
import { PlaceOrderSkeleton } from '@/checkout/components/PlaceOrder/PlaceOrderSkeleton';
import { Button } from '@adobe-commerce/elsie/components';
import { classes } from '@adobe-commerce/elsie/lib';
import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';

export interface PlaceOrderProps extends HTMLAttributes<HTMLDivElement> {
  disabled?: boolean;
  onClick: (event: Event) => Promise<void>;
}

const PlaceOrderComponent: FunctionComponent<PlaceOrderProps> = ({
  children,
  className,
  disabled = false,
  onClick,
}) => {
  return (
    <div className={classes(['checkout-place-order', className])}>
      <Button
        key="placeOrder"
        className="checkout-place-order__button"
        data-testid="place-order-button"
        disabled={disabled}
        size="medium"
        type="button"
        variant="primary"
        onClick={onClick}
      >
        {children}
      </Button>
    </div>
  );
};

export const PlaceOrder = WithConditionals(
  PlaceOrderComponent,
  PlaceOrderSkeleton
);
